.navigation {
  position: fixed;
  z-index: 9;
  box-sizing: border-box;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  padding: 21px 21px 48px;
  transform: translateX(-100%);
  background-color: #2c2c2c;
  transition: all 0.3s;

  &:target {
    transform: translateX(0);
  }

  &__list {
    padding: 0;
  }

  &__item {
    list-style: none;
    max-width: 67px;
    font-weight: 600;
    font-size: 13px;
    letter-spacing: 3px;
    text-transform: uppercase;
    white-space: nowrap;
    transition: all 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__link {
    padding: 20px 0 0;
    font-size: 20px;
    text-decoration: none;
    color: #fff;
    line-height: 60px;

    &:hover {
      color: #2060f6;
    }
  }
}
